export default {
    circle(svg, circleConfig) {
        var g = svg.append('g');

        g.append('circle')
            .attr('r', circleConfig.r)
            .attr('cx', circleConfig.cx)
            .attr('cy', circleConfig.cy)
            .attr('class', circleConfig.styleClass);
    },
    rect(message) {
        console.log('hello from rect ' + message);
    }
}



