d3.json('data/data.json', function(data) {
    var width = 800;
    var height = 600;

    var force = d3.layout.force()
        .size([width, height]);

    var vis = d3.select('#vizContainer')
        .attr('width', width)
        .attr('height', height);

    var linkedByIndex = {};
    var curLinksData;
    var curNodesData;

    function drawD3Document(data) {
        allData = setupData(data);
        linksG = vis.append('g').attr('id', 'links');
        nodesG = vis.append('g').attr('id', 'nodes');

        force.on('tick', forceTick)
            .charge(-200)
            .gravity(.6);

        curNodesData = filterNodes(allData.nodes);
        curLinksData = filterNodes(allData.links, curNodesData);

        force.nodes(curNodesData);
        updateNodes();

        var noForceLinks = [];
        curLinksData.forEach(function(l) {
            if(!l.no_force) {
                noForceLinks.push(l);
            }
        });

        force.links(noForceLinks);
        updateLinks();

        force.start();
    }

    function charge(node) {
        return -Math.pow(node.radius, 2.0) / 2;
    }

    function setupData(data) {
        var circleRadius;
        var countExtent;
        var nodesMap;

        countExtent = d3.extent(data.nodes, function(d) {
            return d.links;
        });

        circleRadius = d3.scale.sqrt()
            .domain(countExtent)
            .range([3, 12]);

        data.nodes.forEach(function(n) {
            var randomNumber;
            n.x = randomNumber = Math.floor(Math.random() * width);
            n.y = randomNumber = Math.floor(Math.random() * height);
            return n.radius = circleRadius(n.links);
        });

        nodesMap = mapNodes(data.nodes);

        data.noForceLinks = [];

        data.links.forEach(function(l) {
            l.source = nodesMap.get(l.source);
            l.target = nodesMap.get(l.target);
            l.no_force = l.no_force || false;
            linkedByIndex['' + l.source.id + ',' + l.target.id] = 1;
        });
        return data;
    }
});



