<template>
    <div id="child">
        <ul>
           <li v-for='item in myList'>
               <input type='checkbox' v-bind:value='item' v-model='controlData.selectedCheck'>
               <label>{{item}}</label>
               <span class="glyphicon glyphicon-star" aria-hidden="true"></span>
           </li>
       </ul>
       <span>{{controlData.selectedCheck | json}}</span>
    </div>
</template>

<script>
export default {
    props: ['controlData'],
    data () {
        return {
            myList: ['blode', 'skiny', 'hot']
        }
    }
}
</script>

<style>
/*body {
  font-family: Helvetica, sans-serif;
